@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(1turn)
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(1turn);
        transform: rotate(1turn)
    }
}

i.spin {
    -webkit-animation: spin .9s linear infinite;
    animation: spin .9s linear infinite
}

